if (document.querySelector(".restaurants")) {
  let restaurants_slider_icon = document.querySelectorAll(".restaurants .slider__icon");

    restaurants_slider_icon.forEach(function (icon) {
      icon.addEventListener("click", function (event) {
        event.preventDefault();
        let parent = document.getElementById(icon.dataset.parent);
        let prev_icon = parent.querySelector(".restaurants .slider__icon--active");
        let restaurants_slider_item = parent.querySelector(".restaurants-slider");
        let restaurants_slider_row = parent.querySelector(".restaurants-slider-row");
        parent.querySelector(".restaurants-slider-controls").classList.toggle("restaurants-slider-controls--module");
        if (restaurants_slider_item) {
          restaurants_slider_item.classList.remove("restaurants-slider");
          restaurants_slider_item.classList.add("restaurants-slider-row");
        } else if (restaurants_slider_row) {
          restaurants_slider_row.classList.remove("restaurants-slider-row");
          restaurants_slider_row.classList.add("restaurants-slider");
        }

        icon.classList.toggle("slider__icon--active");
        prev_icon.classList.toggle("slider__icon--active");
      });
    });

  let restaurants_items = document.querySelectorAll(".restaurants-list__item");
  let reataurants_underline =  document.querySelector('.restaurants .underline');
  let restaurants_item_active = document.querySelector(".restaurants-list__item--active");
  placeLineX(reataurants_underline, {'width': restaurants_item_active.clientWidth + 'px','left': restaurants_item_active.offsetLeft + 'px',});

  restaurants_items.forEach(function (link) {
    link.addEventListener("click", function (event) {
      placeLineX(reataurants_underline, {'width': event.target.clientWidth + 'px', 'left': event.target.offsetLeft + 'px',});
      event.preventDefault();
      let prev_item = document.querySelector(".restaurants-preview-list__item--active");
      let prev_link = document.querySelector(".restaurants-list__item--active");
      let item = document.querySelector(link.getAttribute("href"));
      
      item.classList.toggle("restaurants-preview-list__item--active");
      prev_item.classList.toggle("restaurants-preview-list__item--active");
      prev_link.classList.toggle("restaurants-list__item--active");
      link.classList.toggle("restaurants-list__item--active");
    });
  });
}