import { placeLineX } from "../../../js/import/functions";

if (document.querySelector(".salon-price")) {
  let salon_price_list_items = document.querySelectorAll(".salon-price-list__item");
  let salon_price_underline =  document.querySelector('.salon-price .underline');
  let salon_price_item_active = document.querySelector(".salon-price-list__item--active");
  placeLineX(salon_price_underline, {'width': salon_price_item_active.clientWidth + 'px','left': salon_price_item_active.offsetLeft + 'px',});
  
  salon_price_list_items.forEach(function (link) {
    link.addEventListener("click", function (event) {
      placeLineX(salon_price_underline, {'width': event.target.clientWidth + 'px', 'left': event.target.offsetLeft + 'px',});
      event.preventDefault();
      let prev_item = document.querySelector(".salon-price-preview-list__item--active");
      let prev_link = document.querySelector(".salon-price-list__item--active");
      let item = document.querySelector(link.getAttribute("href"));

      item.classList.toggle("salon-price-preview-list__item--active");
      prev_item.classList.toggle("salon-price-preview-list__item--active");
      prev_link.classList.toggle("salon-price-list__item--active");
      link.classList.toggle("salon-price-list__item--active");
    });
  });
}