if (document.querySelector(".services")) {
  let services_items = document.querySelectorAll(".services-list__item");
  let services_underline =  document.querySelector('.services .underline');
  let services_item_active = document.querySelector(".services-list__item--active");
  placeLineX(services_underline, {'width': services_item_active.clientWidth + 'px','left': services_item_active.offsetLeft + 'px',});

  services_items.forEach(function (link) {
    link.addEventListener("click", function (event) {
      placeLineX(services_underline, {'width': event.target.clientWidth + 'px', 'left': event.target.offsetLeft + 'px',});
      event.preventDefault();
      let prev_service = document.querySelector(".services-preview-list__item--active");
      let prev_link = document.querySelector(".services-list__item--active");
      let service = document.querySelector(link.getAttribute("href"));
      service.classList.toggle("services-preview-list__item--active");
      prev_service.classList.toggle("services-preview-list__item--active");
      prev_link.classList.toggle("services-list__item--active");
      link.classList.toggle("services-list__item--active");
    });
  });
}