let contacts_map_link = document.querySelector('.contacts-map__link');
if (contacts_map_link) {
  contacts_map_link.addEventListener('click', function(event) {
    document.querySelector('.contacts-map').classList.toggle("contacts-map--shown"); 
  })
}

// Yandex Map
if (document.querySelector(".contacts-map")) {
  ymaps.ready(init); 
  function init(){
    let myMap = new ymaps.Map("map",{center: [45.011529, 41.929064],zoom: 16});
    myMap.controls.add("zoomControl").add("typeSelector").add("mapTools");
    let myPlacemark = new ymaps.Placemark([45.011529, 41.929064]);
    myMap.geoObjects.add(myPlacemark);	
  }
}

let contacts_info_titles = document.querySelectorAll('.contacts-info__title');

if (contacts_info_titles) {
  contacts_info_titles.forEach(el => el.addEventListener('click', function() {
    let contacts_info_list = el.parentElement.querySelector('.contacts-info-list');
    contacts_info_list.classList.toggle("contacts-info-list--shown");
    el.classList.toggle("contacts-info__title--shown");
  }))
}