if (document.querySelector('.welcome-down')) {
  document.querySelector('.welcome-down').addEventListener('click', function () {
  let scroll_height = document.querySelector('.welcome').offsetHeight + document.querySelector('.header').offsetHeight - window.pageYOffset;
    window.scrollBy({
        //top: document.documentElement.scrollHeight,
        top: scroll_height,
        behavior: 'smooth'
    });
  });
}
