"use strict";

//import { tns } from "tiny-slider";
import { tns } from "../../../../node_modules/tiny-slider/src/tiny-slider";

if (document.querySelector(".offers-slider")) {
  const offers_slider = tns({
    container: ".offers-slider",
    items: 3.8,
    slideBy: 1,
    //center: true,
    loop: false,
    gutter: 150,
    autoplay: true,
    touch: true,
    mouseDrag: true,
    prevButton: ".offers .slider__prev", 
    nextButton: ".offers .slider__next",
    controlsContainer: ".offers-slider-controls",
    responsive: {
      1279: {
        items: 3.8,
        slideBy: 1,
        gutter: 150,
      },
      1000: {
        items: 3.1,
        slideBy: 1,
        gutter: 100,
      },
      900: {
        items: 2.9,
        slideBy: 1,
        gutter: 80,
      },
      768: {
        items: 2.8,
        slideBy: 1,
        gutter: 70,
      },
      320: {
        items: 4.8,
        slideBy: 1,
        gutter: 100,
      },
    }
  });
}

let offers_next = document.querySelector('.offers .slider__next');
let slider = document.querySelector('.offers-slider-container');
if (offers_next) {
  offers_next.addEventListener('click', function(el) {
    setTimeout(() => {slider.style.overflow = "visible"}, 77.7);
    //slider.style.overflow = "visible";
  })
}
