"use strict";

//import { tns } from "tiny-slider";
import { tns } from "../../../../node_modules/tiny-slider/src/tiny-slider";
import { placeLineX } from "../../../js/import/functions";

if (document.querySelector(".room")) {
  const room_slider = tns({
    container: ".room-slider",
    items: 1,
    slideBy: "page",
    autoplay: true,
    touch: true,
    mouseDrag: true,
    prevButton: ".room .slider__prev", 
    nextButton: ".room .slider__next",
    controlsContainer: ".room-slider-controls",
  });

  let room_titles = document.querySelectorAll(".room-info__title");
  let room_underline =  document.querySelector('.room .underline');
  let room_item_active = document.querySelector(".room-info__title--active");
  placeLineX(room_underline, {'width': room_item_active.clientWidth + 'px','left': room_item_active.offsetLeft + 'px',});

  room_titles.forEach(function (link) {
    link.addEventListener("click", function (event) {
      placeLineX(room_underline, {'width': event.target.clientWidth + 'px', 'left': event.target.offsetLeft + 'px',});
      event.preventDefault();
      let prev_title = document.querySelector(".room-info__title--active");
      let prev_preview = document.querySelector(".room-info-preview--active");
      let preview = document.querySelector(link.getAttribute("href"));
      prev_preview.classList.toggle("room-info-preview--active");
      prev_title.classList.toggle("room-info__title--active");
      preview.classList.toggle("room-info-preview--active");
      link.classList.toggle("room-info__title--active");
    });
  });

  let room_preview_titles = document.querySelectorAll('.room-info-preview__title');

  if (room_preview_titles) {
    room_preview_titles.forEach(el => el.addEventListener('click', function() {
      let room_preview_list = el.parentElement.querySelector('.room-info-preview-inner');
      room_preview_list.classList.toggle("room-info-preview-inner--shown");
      el.classList.toggle("room-info__title--shown");
    }))
  }
}

