let footer_nav_more = document.querySelectorAll('.footer-nav__title');

if (footer_nav_more) {
  footer_nav_more.forEach(el => el.addEventListener('click', function() {
    let footer_nav_list = el.parentElement.querySelector('.footer-nav-list');
    footer_nav_list.classList.toggle("footer-nav-list--shown");
    el.querySelector('.footer-nav__icon').classList.toggle("footer-nav__icon--shown");
  }))
}