import { placeLineX } from "../../../js/import/functions";

if (document.querySelector(".fitness-price")) {
  let fitness_price_list_items = document.querySelectorAll(".fitness-price-list__item");
  let fitness_price_underline =  document.querySelector('.fitness-price .underline');
  let fitness_price_item_active = document.querySelector(".fitness-price-list__item--active");
  placeLineX(fitness_price_underline, {'width': fitness_price_item_active.clientWidth + 'px','left': fitness_price_item_active.offsetLeft + 'px',});

  fitness_price_list_items.forEach(function (link) {
    link.addEventListener("click", function (event) {
      placeLineX(fitness_price_underline, {'width': event.target.clientWidth + 'px', 'left': event.target.offsetLeft + 'px',});
      event.preventDefault();
      let prev_item = document.querySelector(".fitness-price-preview-list__item--active");
      let prev_link = document.querySelector(".fitness-price-list__item--active");
      let item = document.querySelector(link.getAttribute("href"));

      item.classList.toggle("fitness-price-preview-list__item--active");
      prev_item.classList.toggle("fitness-price-preview-list__item--active");
      prev_link.classList.toggle("fitness-price-list__item--active");
      link.classList.toggle("fitness-price-list__item--active");
    });
  });
}